body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: rgb(219, 219, 219);
  font-family: 'Nanum Gothic', sans-serif;
  min-width: 700px;
}

.App {
  height: 100vh;
  overflow: auto;
}

button {
  font-size: 1.2em;
}

.Button-logo {
  position: relative;
  left: 80%;
}

.Title {
  background-color: #fff;
  font-size: 2em;
  margin: 0;
  padding: 0.4em 0.4em 0.4em 3em;
}

.BrgrMenu {
  margin: 0;
  padding: 0;
}

.Title svg {
  float: right;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.Title svg:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.menu {
  float: right;
  height: 100%;
  width: 15%;
  font-size: 1.2em;
  text-align: center;
  background-color: #fff;
}

.menu-enter {
  width: 0;
}

.menu-enter.menu-enter-active {
  width: 15%;
  -webkit-transition: width 200ms ease-in;
  transition: width 200ms ease-in;
}

.menu-leave {
  width: 15%;
}

.menu-leave.menu-leave-active {
  width: 0;
  -webkit-transition: width 200ms ease-in;
  transition: width 200ms ease-in;
}

.menuClose {
  margin: 0.6em;
  height: 30px;
}

.menu svg {
  width: 32px;
  height: 32px;
  float: right;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.menu svg:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.menu p {
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.menu p:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.textBox {
  padding: 0.8em;
}

.Info {
  margin: 5% auto;
  display: block;
  width: 80%;
  height: 90%;
  overflow: auto;
  background-color: rgb(240, 240, 240);
}

.Site-links {
  text-align: left;
  width: 100%;
  height: 50px;
  display: block;
  background-color: rgb(156, 156, 156);
  border: 1px solid rgb(156, 156, 156);
}

.Site-links:hover {
  background-color: rgb(85, 85, 85);
}

.Social-logo {
  width: 50%;
  height: 50px;
  background-color: rgb(156, 156, 156);
  border: 1px solid rgb(156, 156, 156);
}

.Social-logo:hover {
  background-color: rgb(85, 85, 85);
}

img {
}

img.social {
  float: none;
  margin-left: auto;
  margin-right: auto;
}

.formContainer {
  width: 60%;
}

/* Style the label to display next to the inputs */
label {
  padding: 12px 12px 12px 0;
  display: inline-block;
}

/* Style the submit button */
input[type=submit] {
  background-color: #fff;
  color: #000;
  padding: 5px 10px;
  border: 1px solid #000;
  border-radius: 4px;
  cursor: pointer;
}

input[type=submit]:hover{
  color: #fff;
  background-color: rgb(85, 85, 85);
}

#contact table{
  display: inline-block;
}

#contact td{
  font-size: 1.1em;
  padding: 10px;
}

/* Style inputs, select elements and textareas */
input[type=text], select, textarea{
  width: 100%;
  padding: 12px;
  border: 1px solid #000;
  border-radius: 4px;
}

.col-75{
  margin: 1em 0 1em 0;
}

/* unvisited link */
a:link {
  text-decoration: none;
  color: blue;
}

/* visited link */
a:visited {
  color: rgb(66, 66, 66);
}

/* mouse over link */
a:hover {
  color: rgb(85, 85, 85);
}

/* selected link */
a:active {
  color: rgb(85, 85, 85);
}

